.styles-input {
  width: 100%;
  height: 56px;
  background: #f8f8f8;
  box-sizing: border-box;
  border: none;
  padding: 0 27px;
  border-bottom: 3px solid rgba(0, 0, 0, 0.16);
  color: #505050;
  font-size: 16px;
  font-weight: 300;
  font-family: "Segoe UI", 'Toyota Display', sans-serif;
}

.styles-input:focus {
  background: #fcf7f1;
  border-bottom: 3px solid #cf0000;
}

.check input[type=checkbox], .check input[type=radio] {
  display: none;
}

.check label {
  font-size: 12px;
  color: #c2c2c2;
}

.check input[type=checkbox] + label:before {
  content: "\2713";
  color: transparent;
  display: inline-block;
  border: 1px solid #dedede;
  background: #eee;
  font-size: 18px;
  line-height: 18px;
  margin: -5px 5px 0 0;
  height: 18px;
  width: 18px;
  text-align: center;
  vertical-align: middle;
  transition: color ease .3s;
}

.check input[type=checkbox]:checked + label:before {
  color: #000;
}

.select-filter {
  display: inline-block;
  vertical-align: top;
  max-width: 255px;
  width: 100%;
}

.select-wrap {
  position: relative;
  width: 100%;
}

.select__gap {
  background: #f8f8f8;
  color: #505050;
  border-bottom: 3px solid rgba(0, 0, 0, 0.16);
  font-size: 16px;
  height: 56px;
  display: flex;
  align-items: center;
  padding: 0 34px 0 24px;
  cursor: pointer;
  position: relative;
}

.select__gap.on {
  background: #fcf7f1;
  border-bottom: 3px solid #cf0000;
}

.select__list {
  background: #f8f8f8;
  border-top: none;
  position: absolute;
  color: #000;
  width: 100%;
  margin: 0 0;
  z-index: 143;
}

.select__list.on {
  display: block;
}

.select__item span {
  display: block;
  padding: 10px 27px;
  cursor: pointer;
  color: #505050;
  font-size: 16px;
}

.select__gap:after {
  content: '';
  position: absolute;
  right: 22px;
  bottom: 21px;
  z-index: 2;
  width: 0;
  height: 0;
  border-style: solid;
  transition: all .3s ease;
  border-width: 10px 6px 0 6px;
  border-color: #505050 transparent transparent transparent;
}

.on.select__gap:after {
  transform: rotate(180deg);
}

