.header .mobile-menu-toggle {
  z-index: 877;
  margin-top: 23px;
  position: relative;
  margin-right: 15px;
  width: 40px;
  height: 40px;
  display: inline-block;
  cursor: pointer;
  transition: all .5s;
  text-align: left;
}

.header .mobile-menu-toggle div,
.header .mobile-menu-toggle div:before,
.header .mobile-menu-toggle div:after {
  background: #12191f;
  position: absolute;
  height: 3px;
  width: 40px;
  border-radius: 4px;
  transition: all .5s;
}

.header .mobile-menu-toggle div:first-child {
  top: 0;
}

.header .mobile-menu-toggle div:nth-child(2) {
  top: 10px;
}

.header .mobile-menu-toggle div:nth-child(3) {
  top: 20px;
}

.header .mobile-menu-toggle.active div {
  background: #ffffff;
}

.header .mobile-menu-toggle.active div:first-child {
  top: 6%;
  left: 7%;
  width: 50%;
  transform: rotate(45deg);
  transition: width .15s, top .15s .15s, left .15s .15s, transform .15s .15s;
}

.header .mobile-menu-toggle.active div:nth-child(2) {
  transform: rotate(-45deg);
  transition: transform .15s .15s;
}

.header .mobile-menu-toggle.active div:nth-child(3) {
  width: 50%;
  right: 7%;
  top: 45%;
  transform: rotate(45deg);
  transition: width .15s, top .15s .15s, right .15s .15s, transform .15s .15s;
}

